  .custom-select {
    width: 100%;
  }
  .list-panel .add-btn {
    padding-top: 5px;
  }
  .list-panel ul.field-panel {
    border: 1px solid #ccc;
    margin: 5px 0;
    width: 110px;
    border-radius: 5px;
    background: #f4f4f4;
    padding: 5px 0;
  }
  .list-panel ul.field-panel li {
    padding: 0px 15px;
    cursor: pointer;
  }
  .list-panel ul.field-panel li:hover {
    background: wheat;
  }
  .list-panel .sub-fields{
    list-style-type: none;
  }
  .list-panel .sub-fields li {
    position: relative;
    /*margin: 5px 0;*/
    margin-left: -38px;    
  }
  .list-panel .sub-fields li p {
    width: 220px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background: #f4f4f4;
    cursor: pointer;
    padding-left: 10px;
    margin:2px;
    line-height:34px; 
  }
  .list-panel .sub-fields li p:hover {
    background: #ccc;
  }
  .list-panel .sub-fields li span {
    display: inline-block;
    position: absolute;
    border-radius: 50%;
    color: white;
    cursor: pointer;
    width: 20px;
    height: 20px;
    text-align: center;
    padding: 0;
    top: 6px;
    line-height: 20px;
  }
  .list-panel .sub-fields li span i{
    font-weight: bold;
    font-size: 12px;
  }
  .list-panel .sub-fields li span.delete {
    background: #d20000;
    right: 15px;
  }
  .list-panel .sub-fields li span.delete:hover {
    background: #9e0000;
  }
  .list-panel .button-new-tag {
    height: 24px;
    line-height: 22px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .list-actions .ant-input {
    width: 70%;
  }
  .list-panel .actions-list li{
    margin: 8px 0px;
    position: relative;
  }
  .list-panel .actions-list li span{
    display: inline-block;
    position: absolute;
    border-radius: 50%;
    color: white;
    cursor: pointer;
    width: 20px;
    height: 20px;
    text-align: center;
    padding: 0;
    top: 5px;
    line-height: 20px;
  }
  .list-panel .actions-list li span.delete{
    background: #d20000;
    right: 0px;
  }
  .list-panel .actions-list li span.delete:hover {
    background: #9e0000;
  }
  .list-panel .actions-list li span.isedit{
    background: #67c23a;
    right: 30px;
  }
  .list-panel .actions-list li p.action-desc{
    border: 1px solid #ccc;
    border-radius: 5px;
    background: #f4f4f4;
    cursor: pointer;
    width: 70%;
    padding: 0px 5px;
  }
  .list-panel .actions-list li span.ok{
    background: #909399;
    right: 30px;
  }
  .list-panel .actions-list li span i {
    font-weight: bold;
  }
